.Layout {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%; }
  .Layout .Content {
    flex: 1 1;
    display: grid;
    grid-gap: 10px;
    gap: 10px;
    width: 100%;
    background: #ffffff;
    color: #fff;
    font-family: "Merriweather Sans", sans-serif; }
    .Layout .Content a {
      color: inherit; }
    .Layout .Content .col {
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden; }
      .Layout .Content .col:hover {
        cursor: -webkit-grab;
        cursor: grab; }
        .Layout .Content .col:hover .background-image {
          -webkit-transform: scale(2);
                  transform: scale(2);
          -webkit-transition: -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
          transition: -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
          transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
          transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95), -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95); }
        .Layout .Content .col:hover .content {
